import { getSingleNote } from "@/lib/api";


type Props = {
    params: Promise<{id: string}>;
};


const newFolder = async ({params}: Props) => {
    const {id} = await params;
    const note = await getSingleNote(id);
    console.log(note);
    return (
        <>
           <p>I am new folder </p>
        </>
    )
}
export default newFolder